import { Component, h, Host } from '@stencil/core';

/**
 * Basic example
 *
 * The value is passed as a string
 */
@Component({
    tag: 'limel-example-hotkey-basic',
    shadow: true,
    styleUrl: 'hotkey-example.scss',
})
export class HotkeyBasicExample {
    public render() {
        return (
            <Host>
                <limel-hotkey value="ctrl+s" />
                <limel-hotkey value="cmd+s" />
                <limel-hotkey value="option+enter" />
                <limel-hotkey value="option+enter" />
                <limel-hotkey value="cmd+s" />
                <limel-hotkey value="cmd++" />
            </Host>
        );
    }
}
